.news-article {
    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__header-img {
        display: block;
        margin-left: 40px;
        width: 715px;
        height: auto;
        max-height: 480px;
        object-fit: cover;
        object-position: top center;

        @media (max-width: 1400px) {
            width: 100%;
            margin-left: 0;
        }
    }

    &__header-wrapper {
        display: flex;
        justify-content: space-between;
        margin-bottom: 64px;

        @media (max-width: 1400px) {
            flex-direction: column;
        }

        @media (max-width: 540px) {
            margin-bottom: 20px;
        }
    }

    &__header-container {
        flex-direction: column;
        width: 50%;

        @media (max-width: 1400px) {
            width: 100%;
        }
    }

    &__news-content {
        margin-top: 80px;
        margin-bottom: 96px;

        @media (max-width: 540px) {
            margin-top: 40px;
            margin-bottom: 32px;
        }
    }

    &__header-title {
        position: relative;
        width: 100%;
        font-size: 50px;
        line-height: 1.2em;
        margin-bottom: 80px;

        @media (max-width: 540px) {
            margin-bottom: 20px;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            bottom: -40px;
            left: 0;
            width: 135px;
            height: 2px;
            background-color: #0a6d36;

            @media (max-width: $tablet-width) {
                bottom: -10px;
                left: 0;
                width: 55px;
                height: 1px;
            }
        }
    }

    &__header-description {
        margin: 0;
        font-size: 30px;
        font-weight: 600;

        @media (max-width: 1400px) {
            margin-bottom: 40px;
        }

        @media (max-width: 540px) {
            font-size: 11px;
            margin-bottom: 10px;
        }
    }

    &__news-text {
        @media (max-width: 540px) {
            font-size: 11px;
        }
    }

    &__text-item_list {
        padding-left: 23px;

        @media (max-width: 540px) {
            padding-left: 12px;
        }
    }

    &__all-news {
        @media (max-width: 540px) {
            display: none;
        }
    }

    &__button {
        display: none;

        @media (max-width: 540px) {
            display: block;
            position: relative;
            width: 55px;
            padding-left: 30px;
            margin-top: 20px;
            color: $basic-white;
            text-transform: uppercase;
            transition: color $transition-duration;
            font-size: 11px;
            font-weight: 500;
        }

        &::after {
            @media (max-width: $tablet-width) {
                content: "";
                display: block;
                position: absolute;
                z-index: 3;
                top: 2px;
                right: -30px;
                width: 21px;
                height: 21px;
                transition: color $transition-duration;
                background-image: url("../img/news-arrow-white-mobile.png");
                background-repeat: no-repeat;

                @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                    background-image: url("../img/news-arrow-white-mobile@2x.png");
                    background-size: contain;
                }
            }
        }

        &::before {
            @media (max-width: $tablet-width) {
                content: "";
                display: block;
                position: absolute;
                z-index: -3;
                top: -5px;
                left: 0;
                width: 140px;
                height: 37px;
                opacity: 0.9;
                background-image: url("../img/green-button-long.png");
                background-repeat: no-repeat;

                @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                    background-image: url("../img/green-button-long@2x.png");
                    background-size: contain;
                }
            }
        }
    }

    &__list {
        display: flex;

        @media (max-width: 900px) {
            display: block;
        }
    }

    &__article-content {
        margin-left: 80px;

        @media (max-width: 1400px) {
            justify-content: flex-start;
            max-width: 100%;
            margin-left: 50px;
        }

        @media (max-width: 900px) {
            margin-left: 0;
            margin-top: 70px;
        }

        &:first-child {
            margin-left: 0;

            @media (max-width: 900px) {
                margin-top: 0;
            }
        }

        &:last-child {
            @media (max-width: 1400px) {
                display: none;
            }
        }
    }

    &__list-title {
        margin-top: 120px;
        margin-bottom: 50px;
        line-height: 1em;
        font-size: 55px;
        text-transform: uppercase;
        font-weight: 500;
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        align-self: center;

        @media (max-width: 768px) {
            font-size: 38px;
        }

        @media (max-width: 540px) {
            font-size: 20px;
            margin-bottom: 20px;
        }
    }
}
