.partners {
    &__wrapper {
        padding-bottom: 125px;
        display: flex;
        flex-direction: column;

        @media (max-width: $desktop-width) {
            padding-bottom: 80px;
        }

        @media (max-width: 768px) {
            padding-bottom: 30px;
        }
    }

    &__title {
        margin-bottom: 83px;

        @media (max-width: 768px) {
            margin-bottom: 50px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 25px;
        }
    }

    &__partner-card {
        position: relative;
        margin-bottom: 105px;
        margin-left: auto;
        padding-top: 23px;
        padding-bottom: 90px;
        padding-left: 57px;
        padding-right: 57px;
        width: 83%;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        background-color: $basic-white;

        @media (max-width: $desktop-width) {
            padding-left: 40px;
            padding-right: 40px;
            width: 100%;
        }

        @media (max-width: 768px) {
            margin-bottom: 40px;
            padding-bottom: 50px;
            padding-left: 30px;
            padding-right: 30px;
            font-size: 20px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 18px;
            padding-top: 16px;
            padding-bottom: 30px;
            padding-left: 20px;
            padding-right: 20px;
            width: 94%;
            font-size: 11px;
        }
    }

    &__main-info-wrapper {
        display: flex;
        justify-content: space-between;

        @media (max-width: $desktop-width) {
            flex-direction: column;
        }

        @media (max-width: $tablet-width) {
            flex-direction: row;
        }
    }

    &__image-wrapper-main {
        position: relative;
        margin-left: -24.5%;
        width: 584px;
        height: 374px;
        display: flex;
        align-self: flex-start;

        @media (max-width: 1500px) {
            width: 442px;
            height: 283px;
        }

        @media (max-width: $desktop-width) {
            margin: 0;
            align-self: center;
        }

        @media (max-width: $tablet-width) {
            margin-left: -60px;
            width: 140px;
            height: 90px;
            align-self: flex-start;
        }

        @media (max-width: 430px) {
            width: 120px;
            height: 77px;
        }

        &:after {
            position: absolute;
            content: '';
            bottom: 8%;
            right: 10%;
            width: 35.6%;
            height: 22%;
            background-color: $basic-green;
            opacity: 0.8;
            clip-path: polygon(18% 0%, 100% 0%, 82% 100%, 0% 100%);
        }
    }

    &__image-wrapper {
        width: 100%;
        clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
        overflow: hidden;
    }

    &__main-image {
        width: 100%;
        height: auto;
        display: block;
    }

    &__indent {
        margin-left: auto;
        padding-top: 67px;
        max-width: 68%;

        @media (max-width: $desktop-width) {
            margin-left: 0;
            max-width: none;
        }

        @media (max-width: 768px) {
            padding-top: 30px;
        }

        @media (max-width: $tablet-width) {
            padding-top: 10px;
            max-width: 73%;
        }
    }

    &__name {
        margin-bottom: 18px;

        @media (max-width: $desktop-width) {
            text-align: center;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 5px;
            text-align: left;
        }
    }

    &__who {
        margin: 0;
        margin-bottom: 27px;
        font-family: 'Oswald', 'Open Sans', sans-serif;
        line-height: 1em;
        font-size: 36px;
        font-weight: 400;
        color: $basic-green;

        @media (max-width: $desktop-width) {
            text-align: center;
        }

        @media (max-width: 768px) {
            font-size: 28px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 10px;
            text-align: left;
            font-size: 13px;
        }
    }

    &__paragraph {
        margin: 0;
        margin-bottom: 35px;

        &_quote {
            font-weight: 800;
        }

        &_mobile {
            @media (min-width: $tablet-width + 1px) {
                display: none;
            }
        }

        &_introduction {
            @media (max-width: $tablet-width) {
                text-align: justify;
            }
        }

        @media (max-width: 768px) {
            margin-bottom: 20px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 12px;
        }
    }

    &__bottom-image {
        margin-top: 50px;
        margin-left: auto;
        margin-right: auto;
        width: 70.3%;
        height: auto;
        display: block;
        background-color: #ccd1db;

        @media (max-width: 768px) {
            margin-top: 30px;
        }

        @media (max-width: $tablet-width) {
            margin-top: 13px;
            width: 90%;
        }
    }

    &__others-wrapper {
        @media (max-width: $tablet-width) {
            display: none;
        }
    }

    &__others-title {
        position: relative;
        margin-bottom: 65px;
        padding-right: 25px;
        display: inline-block;

        &:after {
            position: absolute;
            content: '';
            bottom: -10px;
            right: 0;
            width: 80%;
            height: 2px;
            background-color: $basic-green;
        }
    }

    &__others-list {
        margin-left: auto;
        margin-right: auto;
        max-width: 66%;
        display: flex;
        justify-content: space-around;
        list-style-type: none;
    }

    &__others-partner {
        flex-basis: 0;
        flex-grow: 1;
    }

    &__partner-link {
        text-decoration: none;
        color: inherit;
    }

    &__others-name {
        margin: 0;
        font-family: 'Oswald', 'Open Sans', sans-serif;
        max-width: 97%;
        line-height: 1em;
        font-size: 35px;
        font-weight: 500;

        @media (max-width: $desktop-width) {
            font-size: 23px;
        }

        @media (max-width: 768px) {
            font-size: 18px;
        }
    }

    &__others-image {
        margin-bottom: 15px;
        width: 100%;
        height: auto;
        display: block;
        clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
    }

    &__others-link {
        display: none;
        @media (max-width: $tablet-width) {
            position: relative;
            display: block;
            max-width: 60px;
            padding-top: 7px;
            padding-bottom: 5px;
            padding-left: 28px;
            padding-right: 53px;
            font-family: 'Oswald', 'Open Sans', sans-serif;
            line-height: 1.1em;
            font-size: 11px;
            text-transform: uppercase;
            background-color: $basic-green;
            color: $basic-white;
            text-decoration: none;
            clip-path: polygon(15% 0%, 100% 0%, 85% 100%, 0% 100%);

            &:after {
                position: absolute;
                content: '';
                background-image: url('/img/other-news-arrow.svg');
                width: 20px;
                height: 20px;
                top: 8px;
                right: 30px;
            }
        }
    }
}
