.service-section {
    $root: &;

    padding-bottom: 90px;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;

    @media (max-width: 768px) {
        padding-bottom: 90px;
    }

    @media (max-width: 540px) {
        padding-bottom: 30px;
        font-size: 11px;
    }

    &_left {
        #{$root}__text-wrapper {
            order: 2;
            padding-left: 70px;

            @media (max-width: 1200px) {
                padding-left: 0;
            }
        }

        #{$root}__image-wrapper {
            padding-right: 80px;

            @media (max-width: 1500px) {
                padding-right: 0;
            }

            @media (max-width: 1200px) {
                order: -1;
                padding-right: 30px;
            }

            @media (max-width: 540px) {
                padding-right: 10px;
            }

            &_with-shadow {
                padding-left: 100px;

                @media (max-width: 1340px) {
                    padding-left: 0;
                }

                #{$root}__image {
                    @media (max-width: 1500px) {
                        transform: translateX(-80px);
                    }

                    @media (max-width: 1340px) {
                        transform: none;
                    }
                }

                &::after {
                    right: 0;

                    @media (max-width: 1340px) {
                        right: -20px;
                    }
                }
            }
        }
    }

    &_right {
        #{$root}__title::after {
            right: auto;
            left: 0;
        }

        #{$root}__image-wrapper {
            padding-left: 80px;

            @media (max-width: 1500px) {
                padding-left: 0;
            }

            @media (max-width: 1200px) {
                padding-left: 0;
            }

            &_with-shadow {
                padding-left: 0;

                &::after {
                    left: -80px;
                }
            }
        }
    }

    &_management {
        @media (max-width: 1610px) {
            align-items: center;
        }

        @media (max-width: 540px) {
            margin-top: 20px;
        }

        #{$root}__text-wrapper {
            margin-top: 120px;

            @media (max-width: 540px) {
                margin-top: 0;
            }
        }
    }

    &_genetics {
        #{$root}__image-wrapper {
            margin-top: 90px;
            padding-right: 105px;

            @media (max-width: 1500px) {
                padding-right: 40px;
            }

            @media (max-width: 1340px) {
                padding-right: 0;
            }

            @media (max-width: 1200px) {
                margin-top: 0;
            }
        }
    }

    &_reproduction,
    &_assessment {
        flex-wrap: wrap;

        #{$root}__additional-text {
            order: 3;
        }
    }

    &_reproduction {
        padding-top: 80px;

        @media (max-width: 768px) {
            padding-top: 80px;
        }

        @media (max-width: 540px) {
            padding-top: 30px;
        }
    }

    &_feed {
        #{$root}__text-wrapper {
            padding-right: 140px;

            @media (max-width: 1200px) {
                padding-right: 0;
            }
        }
    }

    &__text-wrapper,
    &__image-wrapper {
        box-sizing: border-box;
        flex-shrink: 0;
        width: 50%;
    }

    &__text-wrapper {
        padding-right: 70px;

        @media (max-width: 1200px) {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            width: 100%;
            padding-right: 0;
        }
    }

    &__additional-text {
        width: 80vw;

        @media (max-width: 1200px) {
            width: 100%;
        }
    }

    &__title {
        margin: 0;
        font-size: 52px;
        text-align: left;

        @media (max-width: 1340px) {
            margin-bottom: 0;
        }

        @media (max-width: 1200px) {
            width: 50%;
        }

        @media (max-width: 768px) {
            font-size: 38px;
        }

        @media (max-width: 540px) {
            margin-bottom: 0;
        }

        &::after {
            width: 450px;

            @media (max-width: 1610px) {
                right: auto;
                left: 0;
            }

            @media (max-width: 1200px) {
                width: 240px;
            }

            @media (max-width: 540px) {
                left: 0;
                width: 80px;
            }
        }
    }

    &__subtitle {
        margin-top: 40px;
        margin-bottom: -20px;
        font-size: inherit;

        @media (max-width: 540px) {
            margin-top: 8px;
            margin-bottom: -4px;
        }
    }

    &__paragraph,
    &__list {
        margin: 0;
        margin-top: 40px;

        @media (max-width: 540px) {
            margin-top: 8px;
        }
    }

    &__no-wrap {
        white-space: nowrap;

        @media (max-width: 1500px) {
            white-space: normal;
        }
    }

    &__list {
        padding-left: 23px;

        @media (max-width: 540px) {
            padding-left: 12px;
        }
    }

    &__item {
        margin-bottom: 16px;

        @media (max-width: 540px) {
            margin-bottom: 4px;
        }
    }

    &__image-wrapper {
        @media (max-width: 540px) {
            margin-bottom: 8px;
        }

        &_mobile {
            display: none;

            @media (max-width: 1200px) {
                display: block;
            }
        }

        &_desktop {
            @media (max-width: 1200px) {
                display: none;
            }
        }

        &_with-shadow {
            position: relative;

            @media (max-width: 1200px) {
                margin-bottom: 20px;
            }

            @media (max-width: 540px) {
                margin-bottom: 24px;
            }

            &::after {
                content: "";
                position: absolute;
                z-index: -1;
                top: 50%;
                width: 570px;
                height: 570px;
                transform: translateY(-50%);
                background-color: #c9dabc;

                @media (max-width: 1500px) {
                    width: 480px;
                    height: 480px;
                }

                @media (max-width: 1200px) {
                    width: 380px;
                    height: 380px;
                }

                @media (max-width: 1000px) {
                    width: 340px;
                    height: 340px;
                }

                @media (max-width: 900px) {
                    width: 300px;
                    height: 300px;
                }

                @media (max-width: 540px) {
                    width: 150px;
                    height: 150px;
                }

                @media (max-width: 340px) {
                    width: 120px;
                    height: 120px;
                }
            }
        }
    }

    &__image {
        display: block;
        width: 100%;
        height: auto;
        object-fit: cover;
    }
}
