.r-and-d {
    $root: &;

    padding-bottom: 185px;
    display: flex;
    flex-direction: column;

    @media (max-width: $desktop-width) {
        padding-bottom: 100px;
    }

    @media (max-width: 900px) {
        padding-bottom: 60px;
    }

    @media (max-width: 768px) {
        padding-bottom: 50px;
    }

    @media (max-width: $tablet-width) {
        padding-bottom: 40px;
    }

    &__title {
        margin-bottom: 70px;
        align-self: center;
        /* stylelint-disable */
        &:after,
        &:before {
            /* stylelint-enable */
            width: 250px;

            @media (max-width: 768px) {
                margin-bottom: 120px;
            }

            @media (max-width: $tablet-width) {
                margin-bottom: 80px;
            }
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 30px;
        }
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 160px;
        padding: 0 2.7%;
        list-style-type: none;

        @media (max-width: $desktop-width) {
            margin-bottom: 80px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 25px;
            padding: 0;
        }
    }

    &__links-item {
        margin-bottom: 45px;
        width: 43.8%;
        text-align: center;

        @media (max-width: 1300px) {
            width: 48%;
        }

        @media (max-width: $tablet-width) {
            width: 45%;
            margin-bottom: 13px;
        }
    }

    &__link {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 98px;
        text-decoration: none;
        background-color: $basic-green;

        @media (max-width: 950px) {
            min-height: 90px;
        }

        @media (max-width: $tablet-width) {
            min-height: 55px;
        }

        @media (max-width: 375px) {
            padding-left: 20px;
            padding-right: 20px;
        }
    }

    &__link-text {
        margin: 0;
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        line-height: 1em;
        font-size: 36px;
        color: $basic-white;

        @media (max-width: 1200px) {
            font-size: 28px;
        }

        @media (max-width: $tablet-width) {
            font-size: 16px;
        }
    }

    &__laboratories {
        list-style-type: none;
    }

    &__laboratory {
        margin-bottom: 140px;
        display: flex;
        flex-direction: column;

        @media (max-width: 768px) {
            margin-bottom: 80px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 40px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &:nth-child(2n-1) {
            #{$root}__laboratory-image {
                margin-right: auto;
                clip-path: polygon(0% 0%, 100% 0%, 85% 100%, 0% 100%);
            }

            #{$root}__laboratory-name {
                @media (max-width: 1000px) {
                    margin-left: 0;
                    margin-right: auto;
                    text-align: left;
                }

                &:after {
                    @media (max-width: $tablet-width) {
                        right: auto;
                        left: 35px;
                    }
                }
            }

            #{$root}__more-info {
                margin-right: auto;
            }
        }

        &:nth-child(2n) {
            #{$root}__laboratory-image {
                margin-left: auto;
                clip-path: polygon(15% 0%, 100% 0%, 100% 100%, 0% 100%);
            }

            #{$root}__laboratory-name {
                @media (max-width: 1000px) {
                    margin-right: 0;
                    margin-left: auto;
                    text-align: right;
                }

                &:after {
                    @media (max-width: $tablet-width) {
                        right: 35px;
                        left: auto;
                    }
                }
            }

            #{$root}__more-info {
                margin-left: auto;
            }
        }
    }

    &__laboratory-name {
        position: relative;
        margin-bottom: 110px;
        margin-left: auto;
        margin-right: auto;
        text-align: center;

        @media (max-width: $desktop-width) {
            margin-bottom: 70px;
        }

        @media (max-width: 768px) {
            margin-bottom: 40px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 10px;
        }

        &:after {
            content: "";
            position: absolute;
            bottom: -10px;
            width: 450px;
            height: 3px;
            background-color: $basic-green;

            @media (max-width: 1000px) {
                width: 300px;
            }

            @media (max-width: 768px) {
                width: 200px;
            }

            @media (max-width: $tablet-width) {
                bottom: -3px;
                width: 116px;
                height: 1px;
            }
        }

        &_feeds {
            @media (max-width: 1000px) {
                max-width: 580px;
            }

            @media (max-width: 768px) {
                max-width: 380px;
            }

            @media (max-width: $tablet-width) {
                max-width: 180px;
            }

            &:after {
                right: 105px;
            }
        }

        &_research {
            @media (max-width: 1000px) {
                max-width: 580px;
            }

            @media (max-width: 768px) {
                max-width: 380px;
            }

            @media (max-width: $tablet-width) {
                max-width: 180px;
            }

            &:after {
                right: 105px;
            }
        }

        &_genetics {
            &:after {
                right: -45px;
            }
        }

        &_embryology {
            &:after {
                right: 5px;
            }
        }
    }

    &__laboratory-image {
        margin-bottom: 90px;
        width: 89%;
        height: auto;

        @media (max-width: 768px) {
            margin-bottom: 40px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 10px;
        }
    }

    &__laboratory-info {
        @media (max-width: $tablet-width) {
            display: none;
        }
    }

    &__laboratory-paragraph {
        margin: 0;
        margin-bottom: 43px;
        white-space: pre-line;

        &:last-of-type {
            margin-bottom: 35px;
        }

        @media (max-width: 768px) {
            margin-bottom: 25px;
            font-size: 24px;
        }

        @media (max-width: $tablet-width) {
            font-size: 14px;
        }
    }

    &__laboratory-link {
        text-decoration: none;
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        line-height: 1em;
        font-size: 30px;
        font-weight: 500;
        color: $basic-black;
        text-transform: uppercase;

        @media (max-width: 768px) {
            font-size: 20px;
        }
    }

    &__more-info {
        display: none;

        @media (max-width: $tablet-width) {
            display: block;
            align-self: flex-start;
            position: relative;
            border: 2px solid $basic-green;
            padding: 0 32px;
            padding-top: 3px;
            line-height: 2em;
            font-size: 11px;
            color: $basic-black;
            background-color: transparent;
            text-transform: uppercase;

            /* stylelint-disable */
            &:before,
            &:after {
                /* stylelint-enable */
                content: "";
                position: absolute;
                width: 7px;
                height: 2px;
                background-color: $basic-white;
            }

            &:before {
                top: -2px;
                right: 35px;
            }

            &:after {
                bottom: -2px;
                left: 35px;
            }
        }
    }
}
