.contacts {
    /* stylelint-disable */
    font-family: "Oswald", "Open Sans", sans-serif;
    /* stylelint-enable */

    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__item {
        padding-top: 86px;
        padding-bottom: 86px;

        @media (max-width: $desktop-width) {
            padding-top: 46px;
            padding-bottom: 46px;
        }

        @media (max-width: $tablet-width) {
            padding-top: 12px;
            padding-bottom: 12px;
        }
    }

    &__item-container {
        display: flex;
        align-items: start;
    }

    &__logo-wrapper {
        display: flex;
        flex: none;
        flex-flow: column;
        align-items: center;
        width: 230px;
        margin-right: 85px;

        @media (max-width: 1340px) {
            width: auto;
            margin-right: 75px;
            margin-left: 100px;
        }

        @media (max-width: 1150px) {
            margin-right: 30px;
            margin-left: 50px;
        }

        @media (max-width: 900px) {
            margin-right: 25px;
            margin-left: 58px;
        }

        @media (max-width: 540px) {
            margin-right: 15px;
            margin-left: 31px;
        }

        &:after {
            content: "";
            display: block;
            width: 3px;
            height: 100%;
            background-color: #0a6d24;

            @media (max-width: 900px) {
                width: 2px;
            }

            @media (max-width: 540px) {
                width: 1px;
            }
        }
    }

    &__logo-img {
        width: 100%;
        margin-bottom: 26px;

        @media (max-width: 1340px) {
            display: none;
        }
    }

    &__logo-img-mobile {
        display: none;

        @media (max-width: 1300px) {
            display: block;
            max-width: 240px;
            margin-bottom: 45px;
        }

        @media (max-width: 1150px) {
            max-width: 120px;
            margin-bottom: 15px;
        }

        @media (max-width: 540px) {
            width: 65px;
        }
    }

    &__person-photo {
        flex: none;
        width: 288px;
        height: 288px;
        object-fit: cover;
        margin-right: 85px;

        @media (max-width: 1150px) {
            margin-right: 30px;
        }

        @media (max-width: 900px) {
            width: 160px;
            height: 160px;
            margin-right: 25px;
        }

        @media (max-width: 540px) {
            width: 102px;
            height: 102px;
            margin-right: 15px;
        }
    }

    &__picture {
        display: flex;
    }

    &__content {
        display: flex;
        align-items: start;
    }

    &__info {
        margin-top: 20px;

        @media (max-width: 1150px) {
            margin-top: 0;
        }
    }

    &__person-name {
        font-weight: 500;
        font-size: 52px;
        line-height: 1.2em;
        text-transform: uppercase;
        letter-spacing: 0.02em;

        @media (max-width: 1150px) {
            font-size: 39px;
        }

        @media (max-width: 900px) {
            font-size: 32px;
        }

        @media (max-width: 600px) {
            font-size: 26px;
        }

        @media (max-width: 540px) {
            font-size: 16px;
        }
    }

    &__person-post {
        margin-top: 5px;
        margin-bottom: 47px;
        font-size: 34px;
        line-height: 1.2em;
        font-weight: 300;
        color: $basic-green;
        letter-spacing: 0.02em;

        @media (max-width: 900px) {
            font-size: 24px;
            margin-top: 0;
            margin-bottom: 35px;
        }

        @media (max-width: 540px) {
            font-size: 12px;
            margin-bottom: 10px;
            max-width: 150px;
        }
    }
    /* stylelint-disable */
    &__person-phone,
    &__person-email {
        /* stylelint-enable */
        position: relative;
        padding-left: 64px;
        font-weight: 500;
        font-size: 39px;
        line-height: 1em;
        text-transform: uppercase;
        text-decoration: none;
        color: #000000;
        transition: color $transition-duration;

        @media (max-width: 1150px) {
            font-size: 29px;
            padding-left: 50px;
        }

        @media (max-width: 900px) {
            font-size: 24px;
        }

        @media (max-width: 600px) {
            font-size: 20px;
            padding-left: 24px;
        }

        @media (max-width: 540px) {
            font-size: 15px;
        }

        @media (max-width: 380px) {
            font-size: 12px;
        }

        @media (max-width: 330px) {
            font-size: 9px;
        }

        &:hover {
            color: rgba(0, 0, 0, 0.7);
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 45px;
            height: 80%;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center center;

            @media (max-width: 600px) {
                width: 20px;
            }
        }
    }

    &__person-phone::before {
        background-image: url("../img/phone-icon.svg");

        @media (max-width: 1150px) {
            margin-top: 3px;
        }

        @media (max-width: 900px) {
            margin-top: 1px;
        }
    }

    &__person-email::before {
        margin-top: 3px;
        background-image: url("../img/email-icon.svg");

        @media (max-width: 600px) {
            margin-top: 2px;
        }
    }

    &__person-contacts {
        display: grid;
        grid-gap: 16px;

        @media (max-width: 1150px) {
            grid-gap: 24px;
        }

        @media (max-width: 900px) {
            grid-gap: 16px;
        }

        @media (max-width: 600px) {
            grid-gap: 6px;
        }
    }
}
