.knowledge {
    margin-top: 70px;
    position: relative;

    &__container {
        margin-top: 40px;
        width: 100%;
        max-width: 1390px;

        @media (max-width: 1630px) {
            max-width: 92%;
        }

        @media (max-width: 1340px) {
            margin-top: 0;
        }

        @media (max-width: 767px) {
            max-width: 72%;
        }
    }

    &__title {
        &::after {
            width: 440px;

            @media (max-width: 768px) {
                right: 110px;
                width: 300px;
            }

            @media (max-width: $tablet-width) {
                right: 32px;
                width: 200px;
            }
        }
    }

    &__list {
        list-style: none;
    }

    &__prev-button {
        left: 60px;

        @media (max-width: 768px) {
            left: 35px;
        }
    }

    &__next-button {
        right: 60px;

        @media (max-width: 768px) {
            right: 35px;
        }
    }
}
