.services-main {
    padding-top: 95px;

    @media (max-width: 768px) {
        padding-top: 95px;
    }

    @media (max-width: 540px) {
        padding-top: 40px;
    }

    &__container {
        display: flex;
        align-items: start;
        width: calc(100% + 80px);
        margin-left: -80px;

        @media (max-width: 1700px) {
            margin-left: 0;
            width: 100%;
        }

        @media (max-width: 1400px) {
            align-items: center;
        }

        @media (max-width: 1210px) {
            flex-flow: column wrap;
        }
    }

    &__title {
        text-align: left;
        font-size: 46px;
        display: inline-block;
        margin-bottom: 0;

        @media (max-width: 1340px) {
            margin-bottom: 0;
        }

        @media (max-width: 768px) {
            font-size: 38px;
        }

        @media (max-width: 540px) {
            margin-bottom: 0;
            font-size: 18px;
        }

        &:after {
            display: none;
        }
    }

    &__desc {
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        font-weight: 300;
        font-size: 38px;
        margin: 0;
        margin-top: 8px;
        line-height: 1.2em;

        @media (max-width: 850px) {
            font-size: 28px;
        }

        @media (max-width: 540px) {
            margin-top: 0;
            font-size: 15px;
        }
    }

    &__content {
        box-sizing: border-box;
        width: 100%;
        margin-top: 25px;
        margin-right: 55px;
        border: 2px solid $basic-green;
        padding: 40px 20px 40px 80px;

        @media (max-width: 1700px) {
            padding: 40px;
        }

        @media (max-width: 1400px) {
            padding-top: 20px;
            padding-bottom: 20px;
            margin-top: 0;
        }

        @media (max-width: 1210px) {
            margin-right: 0;
            margin-top: 0;
            padding-bottom: 60px;
        }

        @media (max-width: 540px) {
            padding: 10px;
            padding-bottom: 30px;
        }
    }

    &__img {
        display: block;
        flex: none;
        width: 100%;
        max-width: 770px;
        height: auto;

        @media (max-width: 1400px) {
            max-width: 620px;
        }

        @media (max-width: 1210px) {
            width: 70%;
            max-width: 460px;
            margin-left: auto;
            margin-top: -50px;
            margin-right: 40px;
        }

        @media (max-width: 540px) {
            margin-top: -20px;
            margin-right: 20px;
        }
    }
}
