.contacts-footer {
    display: flex;
    justify-content: center;
    padding-top: 160px;
    padding-bottom: 60px;
    /* stylelint-disable */
    font-family: "Oswald", "Open Sans", sans-serif;
    /* stylelint-enable */

    @media (max-width: 768px) {
        padding-top: 24px;
        padding-bottom: 24px;
    }

    @media (max-width: 540px) {
        padding-top: 16px;
        padding-left: 31px;
        justify-content: flex-start;
    }

    &__link {
        position: relative;
        padding-left: 80px;
        font-weight: 500;
        font-size: 39px;
        line-height: 1em;
        text-decoration: none;
        color: #000;
        text-transform: uppercase;
        transition: color $transition-duration;

        @media (max-width: 1150px) {
            padding-left: 74px;
        }

        @media (max-width: 900px) {
            padding-left: 64px;
            font-size: 28px;
        }

        @media (max-width: 540px) {
            padding-left: 30px;
            font-size: 15px;
        }

        &:hover {
            color: rgba(0, 0, 0, 0.7);
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 55px;
            height: 80%;
            margin-top: 2px;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            background-image: url("../img/phone-icon.svg");

            @media (max-width: 900px) {
                width: 45px;
            }

            @media (max-width: 540px) {
                margin-top: 1px;
                width: 20px;
            }
        }
    }
}
