.our-features {
    &__list {
        list-style: none;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 106px;

        @media (max-width: 1480px) {
            grid-gap: 80px;
        }

        @media (max-width: 1380px) {
            grid-gap: 64px;
        }

        @media (max-width: 900px) {
            grid-gap: 36px;
        }

        @media (max-width: 540px) {
            grid-gap: 20px;
        }

        @media (max-width: 370px) {
            grid-gap: 12px;
        }
    }

    &__item {
        position: relative;
        box-sizing: border-box;
        padding: 125px 10px 10px 10px;
        height: 480px;
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        font-size: 48px;
        line-height: 1.3em;
        text-transform: uppercase;
        font-weight: 500;
        text-align: center;
        color: #ffffff;
        background-repeat: no-repeat;
        background-position: bottom center;
        background-size: cover;

        @media (max-width: 1380px) {
            font-size: 38px;
        }

        @media (max-width: 1280px) {
            padding-top: 100px;
            height: 360px;
            font-size: 32px;
        }

        @media (max-width: 900px) {
            padding-top: 88px;
            height: 300px;
            font-size: 24px;
        }

        @media (max-width: 540px) {
            padding: 30px 5px 8px 5px;
            height: 116px;
            font-size: 12px;
        }

        @media (max-width: 370px) {
            font-size: 9px;
            font-weight: 400;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 175px;
            height: 175px;
            background-color: #ffffff;
            background-repeat: no-repeat;
            background-position: center center;
            border-radius: 50%;

            @media (max-width: 1380px) {
                width: 120px;
                height: 120px;
            }

            @media (max-width: 540px) {
                width: 44px;
                height: 44px;
            }
        }

        &_certificate {
            background-image: url("../../img/features-bg-1.png");

            &::after {
                background-image: url("../img/diploma.svg");
                background-size: 116px auto;
                background-position: bottom 20px center;

                @media (max-width: 1380px) {
                    background-size: 70px auto;
                    background-position: bottom 18px center;
                }

                @media (max-width: 540px) {
                    background-size: 28px auto;
                    background-position: bottom 6px center;
                }
            }
        }

        &_laboratories {
            background-image: url("../../img/features-bg-2.png");

            &::after {
                background-image: url("../img/reproductive.svg");
                background-size: 138px auto;

                @media (max-width: 1380px) {
                    background-size: 80px auto;
                }

                @media (max-width: 540px) {
                    background-size: 33px auto;
                }
            }
        }

        &_warranty {
            background-image: url("../../img/features-bg-3.png");

            &::after {
                background-image: url("../img/stonks.svg");
                background-size: 101px auto;

                @media (max-width: 1380px) {
                    background-size: 56px auto;
                }

                @media (max-width: 540px) {
                    background-size: 24px auto;
                }
            }
        }
    }

    &__text {
        font: inherit;
    }
}
