.knowledge-item {
    &__img {
        display: block;
        width: 100%;
        height: auto;
        margin-bottom: 20px;

        @media (max-width: $tablet-width) {
            margin-bottom: 18px;
        }
    }

    &__header-title {
        font-family: $second-font;
        font-size: 26px;
        font-weight: 500;
        max-width: 230px;
        line-height: 28px;
        text-transform: uppercase;
        word-break: break-word;

        @media (max-width: $tablet-width) {
            line-height: 19px;
            font-size: 16px;
        }
    }

    &__header-container {
        min-height: 160px;

        @media (max-width: $tablet-width) {
            min-height: 0;
        }
    }

    &__description {
        max-width: 300px;
        font-size: 18px;
        font-weight: 400;
        margin-top: 8px;
        margin-bottom: 0;
        @media (max-width: $tablet-width) {
            width: 209px;
            font-size: 13px;
        }
    }

    &__link {
        display: inline-block;
        position: relative;
        margin: 0;
        margin-top: 10px;
        padding-right: 46px;
        color: $basic-black;
        text-transform: uppercase;
        font-family: $second-font;
        font-size: 23px;
        font-weight: 500;
        cursor: pointer;

        @media (max-width: $tablet-width) {
            font-size: 16px;
            padding-right: 30px;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 50%;
            right: 0;
            width: 31px;
            height: 31px;
            transform: translateY(-50%);
            background-size: contain;
            background-image: url("../../img/arrow-right@2x.png");
            background-repeat: no-repeat;

            @media (max-width: $tablet-width) {
                width: 22px;
                height: 22px;
                background-image: url("../../img/arrow-right-mobile@2x.png");
            }
        }
    }
}
