.product-item {
    $root: &;
    padding: 80px 0;

    @media (max-width: 916px) {
        padding: 60px 0;
    }

    @media (max-width: 540px) {
        padding: 40px 0;
    }

    &__container {
        display: flex;
        align-items: start;
        justify-content: space-between;
    }

    &_left {
        #{$root} {
            &__img-mobile {
                float: left;
                margin: 0 19px 10px 10px;
            }

            &__container {
                flex-flow: row-reverse;
            }

            &__img {
                margin-right: 150px;
                margin-left: 40px;
            }
        }
    }

    &__title {
        text-align: left;
        display: inline-block;
        font-size: 52px;
        width: auto;

        @media (max-width: 916px) {
            width: auto;
            font-size: 32px;
            margin-bottom: 12px;
        }

        @media (max-width: 360px) {
            font-size: 14px;
        }

        &:after {
            left: 10%;
            width: 100%;
        }
    }

    &__desc {
        font-size: 30px;
        font-weight: 400;
        margin-top: 40px;
        margin-bottom: 45px;

        @media (max-width: 916px) {
            font-size: 18px;
            margin-top: 18px;
            margin-bottom: 18px;
            line-height: 1.2em;
        }

        @media (max-width: 540px) {
            font-size: 12px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__img {
        margin-right: 40px;
        margin-left: 150px;
        flex: none;
        max-width: 360px;

        @media (max-width: 1300px) {
            display: none;
        }
    }

    &__img-mobile {
        display: none;
        float: right;
        margin: 0 10px 10px 19px;

        @media (max-width: 1300px) {
            display: block;
            max-width: 250px;
        }

        @media (max-width: 916px) {
            max-width: 190px;
        }

        @media (max-width: 540px) {
            max-width: 105px;
            margin-bottom: 10px;
        }

        @media (max-width: 360px) {
            max-width: 85px;
        }
    }

    &__btn {
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        box-sizing: border-box;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 2;
        width: 220px;
        min-height: 95px;
        padding: 5px 24px;
        text-align: center;
        color: #fff;
        font-weight: 600;
        font-size: 20px;
        line-height: 1.2em;
        text-decoration: none;
        text-transform: uppercase;

        @media (max-width: 916px) {
            width: 160px;
            min-height: 65px;
            font-size: 12px;
            line-height: 1.2em;
        }

        @media (max-width: 620px) {
            width: 130px;
            font-size: 10px;
        }

        @media (max-width: 540px) {
            width: 85px;
            min-height: 35px;
            font-size: 8px;
            padding: 5px 10px;
        }

        @media (max-width: 360px) {
            display: flex;
            justify-content: center;
            width: 100%;
            min-height: 0;
            padding: 10px;
        }
    }

    &__btn-box {
        display: inline-block;
        position: relative;
        margin-right: 50px;

        @media (max-width: 1450px) {
            margin-right: 10px;
            margin-bottom: 5px;
        }

        @media (max-width: 1300px) {
            margin-right: 0;
            margin-bottom: 10px;
        }

        @media (max-width: 360px) {
            display: block;
            margin-right: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        &:after {
            content: "";
            display: block;
            position: absolute;
            z-index: 1;
            background-color: #0a6d24;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            -webkit-transform: skew(-24deg);
            -moz-transform: skew(-24deg);
            -o-transform: skew(-24deg);
        }
    }

    &__btn-list {
        box-sizing: border-box;
        display: flex;
        flex-flow: row wrap;
        margin-top: 62px;

        @media (max-width: 1300px) {
            justify-content: space-between;
            width: 100%;
            padding: 0 25px;
            margin-top: 19px;
            padding-top: 20px;
        }

        @media (max-width: 540px) {
            margin-top: 20px;
            padding: 0 10px;
        }

        @media (max-width: 360px) {
            display: block;
        }
    }

    &__modal-closed {
        display: none;
    }

    &__modal {
        position: fixed;
        z-index: 5;
        top: 50%;
        left: 50%;
        width: 1100px;
        max-height: 100%;
        overflow: scroll;
        padding: 0 25px 45px 25px;
        color: $basic-white;
        transform: translate(-50%, -50%);
        text-align: left;
        background-image: url("../img/modal-bg@1x.jpg");
        background-position-y: center;
        background-repeat: no-repeat;
        background-size: cover;

        @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
            background-image: url("../img/modal-bg@2x.jpg");
        }

        @media (max-width: 1200px) {
            width: 90%;
            padding: 0 20px 20px 20px;
            transform: translate(-50%, -50%);
        }

        @media (max-width: 900px) {
            padding: 0 20px 20px 20px;
        }

        @media (max-width: 500px) {
            padding: 0 10px 20px 10px;
        }
    }

    &__table {
        width: 100%;
        border-spacing: 0;
    }

    &__modal-title {
        font-size: 28px;
        font-weight: 400;
        margin-top: 60px;
        margin-bottom: 20px;
        padding-left: 10px;
        text-transform: uppercase;

        @media (max-width: 1200px) {
            margin-top: 30px;
        }

        @media (max-width: 900px) {
            margin-top: 20px;
            font-size: 20px;
        }

        @media (max-width: 500px) {
            font-size: 13px;
            margin-bottom: 10px;
        }
    }

    &__button-close {
        position: absolute;
        top: 10px;
        right: 20px;
        width: 50px;
        height: 50px;
        border: none;
        cursor: pointer;
        background-color: transparent;

        @media (max-width: 1200px) {
            top: 0;
            right: 10px;
            width: 40px;
            height: 40px;
        }

        @media (max-width: 500px) {
            top: 5px;
            right: 10px;
            width: 20px;
            height: 20px;
        }

        &:focus {
            outline: none;
        }
    }
    /* stylelint-disable */
    &__button-close::before,
    &__button-close::after {
        /* stylelint-enable */
        content: "";
        position: absolute;
        width: 20px;
        height: 2px;
        background: $basic-white;

        @media (max-width: 900px) {
            width: 16px;
            height: 2px;
        }

        @media (max-width: 500px) {
            width: 10px;
            height: 2px;
        }
    }

    &__button-close:before {
        transform: rotate(45deg);
    }

    &__button-close:after {
        transform: rotate(-45deg);
    }

    &__table-data {
        width: 50%;
        padding: 5px 5px 10px 10px;
        font-size: 24px;
        font-weight: 400;
        border-top: 3px $basic-white solid;

        @media (max-width: 900px) {
            font-size: 18px;
            border-top: 2px $basic-white solid;
        }

        @media (max-width: 500px) {
            font-size: 11px;
            padding: 2px 5px 5px 5px;
        }
    }

    &__table-row:last-child &__table-data {
        border-bottom: 3px $basic-white solid;

        @media (max-width: 900px) {
            border-top: 2px $basic-white solid;
        }
    }

    &__right-cell {
        border-right: 3px $basic-white solid;

        @media (max-width: 900px) {
            border-top: 2px $basic-white solid;
        }
    }

    &__left-cell {
        border-left: 3px $basic-white solid;

        @media (max-width: 900px) {
            border-top: 2px $basic-white solid;
        }
    }

    &__modal-list {
        width: 90%;
        padding-left: 25px;
        list-style: none;
    }

    &__modal-list-item {
        margin-bottom: 25px;
        padding-left: 7px;
        font-size: 24px;

        @media (max-width: 900px) {
            font-size: 18px;
            margin-bottom: 15px;
        }

        @media (max-width: 500px) {
            font-size: 11px;
            margin-bottom: 10px;
        }
    }

    &__modal-list-item::marker {
        content: "-";
        display: block;
        width: 8px;
        height: 2px;
    }
}
